import { Address, Client, encodeFunctionData } from 'viem';
import { MultiSigDeleGator } from '@metamask-private/delegation-abis';
import { simulateContract } from 'viem/actions';

export const simulate = (
  client: Client,
  multiSigDeleGatorAddress: Address,
  signer: Address,
) => {
  return simulateContract(client, {
    address: multiSigDeleGatorAddress,
    abi: MultiSigDeleGator.abi,
    functionName: 'addSigner',
    args: [signer],
  });
};

export const encode = (signer: Address) => {
  return encodeFunctionData({
    abi: MultiSigDeleGator.abi,
    functionName: 'addSigner',
    args: [signer],
  });
};
