import { Address, Client, encodeFunctionData } from 'viem';
import { DelegationManager } from '@metamask-private/delegation-abis';
import {
  DelegationStruct,
  encodePermissionContexts,
  ExecutionMode,
  ExecutionStruct,
  encodeExecutionCalldatas,
} from '@metamask-private/delegation-utils';
import { simulateContract, writeContract } from 'viem/actions';

import { InitializedClient } from '../../types';

export const simulate = (
  client: Client,
  contractAddress: Address,
  delegations: DelegationStruct[][],
  modes: ExecutionMode[],
  executions: ExecutionStruct[][],
) => {
  return simulateContract(client, {
    address: contractAddress,
    abi: DelegationManager.abi,
    functionName: 'redeemDelegations',
    args: [
      encodePermissionContexts(delegations),
      modes,
      encodeExecutionCalldatas(executions),
    ],
  });
};

export const execute = async (
  client: InitializedClient,
  contractAddress: Address,
  delegations: DelegationStruct[][],
  modes: ExecutionMode[],
  executions: ExecutionStruct[][],
) => {
  const { request } = await simulate(
    client,
    contractAddress,
    delegations,
    modes,
    executions,
  );
  return writeContract(client, request);
};

export const encode = (
  delegations: DelegationStruct[][],
  modes: ExecutionMode[],
  executions: ExecutionStruct[][],
) => {
  return encodeFunctionData({
    abi: DelegationManager.abi,
    functionName: 'redeemDelegations',
    args: [
      encodePermissionContexts(delegations),
      modes,
      encodeExecutionCalldatas(executions),
    ],
  });
};
