import { Address, Client, encodeFunctionData } from 'viem';
import { DeleGatorCore } from '@metamask-private/delegation-abis';
import { ExecutionStruct } from '@metamask-private/delegation-utils';
import { simulateContract } from 'viem/actions';

export const simulate = (
  client: Client,
  contractAddress: Address,
  execution: ExecutionStruct,
) => {
  return simulateContract(client, {
    address: contractAddress,
    abi: DeleGatorCore.abi,
    functionName: 'execute',
    args: [execution],
  });
};

export const encode = (execution: ExecutionStruct) => {
  return encodeFunctionData({
    abi: DeleGatorCore.abi,
    functionName: 'execute',
    args: [execution],
  });
};
