import { Address, Client, encodeFunctionData } from 'viem';
import { DelegationManager } from '@metamask-private/delegation-abis';
import { DelegationStruct } from '@metamask-private/delegation-utils';
import { simulateContract, writeContract } from 'viem/actions';

import { InitializedClient } from '../../types';
import { NarrowAbiToFunction } from 'src/DelegationFramework/utils';

export const simulate = (
  client: Client,
  contractAddress: Address,
  delegation: DelegationStruct,
) => {
  const abi = DelegationManager.abi as any as NarrowAbiToFunction<
    typeof DelegationManager.abi,
    'disableDelegation'
  >;

  return simulateContract(client, {
    address: contractAddress,
    abi,
    functionName: 'disableDelegation',
    args: [delegation],
  });
};

export const execute = async (
  client: InitializedClient,
  contractAddress: Address,
  delegation: DelegationStruct,
) => {
  const { request } = await simulate(client, contractAddress, delegation);
  return writeContract(client, request);
};

export const encode = (delegation: DelegationStruct) => {
  return encodeFunctionData({
    abi: DelegationManager.abi,
    functionName: 'disableDelegation',
    args: [delegation],
  });
};
