import { Address, Hex, Client, encodeFunctionData } from 'viem';
import { SimpleFactory } from '@metamask-private/delegation-abis';
import { simulateContract, writeContract } from 'viem/actions';

import { InitializedClient } from '../../types';

export const simulate = (
  client: Client,
  factoryAddress: Address,
  creationCode: Hex,
  salt: Hex,
) => {
  return simulateContract(client, {
    address: factoryAddress,
    abi: SimpleFactory.abi,
    functionName: 'deploy',
    args: [creationCode, salt],
  });
};

export const encode = (creationCode: Hex, salt: Hex) => {
  return encodeFunctionData({
    abi: SimpleFactory.abi,
    functionName: 'deploy',
    args: [creationCode, salt],
  });
};

export const execute = async (
  client: InitializedClient,
  factoryAddress: Address,
  creationCode: Hex,
  salt: Hex,
) => {
  const { request } = await simulate(
    client,
    factoryAddress,
    creationCode,
    salt,
  );
  return writeContract(client, request);
};
