import { Address, Client, encodeFunctionData } from 'viem';
import { DeleGatorCore } from '@metamask-private/delegation-abis';
import { DelegationStruct } from '@metamask-private/delegation-utils';
import { simulateContract } from 'viem/actions';
import { NarrowAbiToFunction } from '../../utils';

export const simulate = (
  client: Client,
  contractAddress: Address,
  delegation: DelegationStruct,
) => {
  const abi = DeleGatorCore.abi as any as NarrowAbiToFunction<
    typeof DeleGatorCore.abi,
    'disableDelegation'
  >;

  return simulateContract(client, {
    address: contractAddress,
    abi,
    functionName: 'disableDelegation',
    args: [delegation],
  });
};

export const encode = (delegation: DelegationStruct) => {
  return encodeFunctionData({
    abi: DeleGatorCore.abi,
    functionName: 'disableDelegation',
    args: [delegation],
  });
};
