import { Address, Client, encodeFunctionData } from 'viem';
import { Ownable2Step } from '@metamask-private/delegation-abis';
import { simulateContract, writeContract } from 'viem/actions';

import { InitializedClient } from '../../types';

export const simulate = (
  client: Client,
  contractAddress: Address,
  account: Address,
) => {
  return simulateContract(client, {
    address: contractAddress,
    abi: Ownable2Step.abi,
    functionName: 'transferOwnership',
    args: [account],
  });
};

export const execute = async (
  client: InitializedClient,
  contractAddress: Address,
  account: Address,
) => {
  const { request } = await simulate(client, contractAddress, account);
  return writeContract(client, request);
};

export const encode = (account: Address) => {
  return encodeFunctionData({
    abi: Ownable2Step.abi,
    functionName: 'transferOwnership',
    args: [account],
  });
};
