import { HybridDeleGator } from '@metamask-private/delegation-abis';
import { Address, Client } from 'viem';
import { readContract } from 'viem/actions';

export const read = async (client: Client, hybridDeleGatorAddress: Address) =>
  await readContract(client, {
    address: hybridDeleGatorAddress,
    abi: HybridDeleGator.abi,
    functionName: 'getKeyIdHashes',
  });
