import { Address, Client, encodeFunctionData } from 'viem';
import { HybridDeleGator } from '@metamask-private/delegation-abis';
import { simulateContract } from 'viem/actions';

import { P256Owner } from '../../types';

export const simulate = (
  client: Client,
  hybridDeleGatorAddress: Address,
  eoaOwner: Address,
  p256Owners: P256Owner[],
) => {
  return simulateContract(client, {
    address: hybridDeleGatorAddress,
    abi: HybridDeleGator.abi,
    functionName: 'updateSigners',
    args: [
      eoaOwner,
      p256Owners.map((p256Owner) => p256Owner.keyId),
      p256Owners.map((p256Owner) => p256Owner.x),
      p256Owners.map((p256Owner) => p256Owner.y),
    ],
  });
};

export const encode = (eoaOwner: Address, p256Owners: P256Owner[]) => {
  return encodeFunctionData({
    abi: HybridDeleGator.abi,
    functionName: 'updateSigners',
    args: [
      eoaOwner,
      p256Owners.map((p256Owner) => p256Owner.keyId),
      p256Owners.map((p256Owner) => p256Owner.x),
      p256Owners.map((p256Owner) => p256Owner.y),
    ],
  });
};
